import React, { Component } from 'react';
import './Experience.css';
import Modal from 'react-modal';
import data from '../data/experience_desc.json'
import cabin from '../images/experience/cabin.png'
import cake from '../images/experience/cake.png'
import circus from '../images/experience/circus.png'
import game from '../images/experience/game.png'
import safe from '../images/experience/safe.png'
import submarine from '../images/experience/submarine.png'

const CreateModalRow = props => {
  return (
    <div className = "row">
      {props.items.map((x, i) => (
        <ModalTile key={i} onClick={props.onClickOpen(i)} caption={x} src={props.src[i]} type={props.type[i]} />
      ))}
    </div>
  );
}

const ModalTile = props => {
  return (
    <div className="col-md-6 col-lg-4">
      <a className="portfolio-item d-block mx-auto" href="#portfolio-modal-1" onClick={props.onClick}>
        <div className="portfolio-item-caption d-flex position-absolute h-100 w-100">
          <div className="portfolio-item-caption-content my-auto w-100 text-center text-white">
            <i className="fas fa-search-plus fa-3x"></i>
          </div>
        </div>
        <img className="img-fluid" src={props.src} alt="" />
      </a>
      <ModalCaption caption={props.caption} type={props.type} />
    </div>
  );
}

const ModalCaption = props => {
  return (
    <div className="portfolio-caption">
      <h4>{props.caption}</h4>
      <p className="text-muted">{props.type}</p>
    </div>
  );
}

const ModalContent = props => {
  return (
    <div className="portfolio-modal portfolio-modal-dialog bg-white" id="portfolio-modal-1">
        <a className="close-button d-none d-md-block" onClick={props.onClick}>
          <i className="fas fa-3x fa-times"></i>
        </a>
        <div className="container text-center">
          <div className="row">
            <div className="col-lg-8 mx-auto">
              <h2 className="text-secondary text-uppercase mb-0">Project Name</h2>
              <hr className="star-dark mb-5" />
              <img className="img-fluid mb-5" src={props.src} alt=""/>
              <p className="mb-5">Lorem ipsum dolor sit amet, consectetur adipisicing elit. Mollitia neque assumenda ipsam nihil, molestias magnam, recusandae quos quis inventore quisquam velit asperiores, vitae? Reprehenderit soluta, eos quod consequuntur itaque. Nam.</p>
              <button className="btn btn-primary btn-lg rounded-pill" onClick={props.onClick}>
                Close</button>
            </div>
          </div>
        </div>
    </div>
  );
}

Modal.setAppElement(document.getElementById('root'));

class Experience extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currentItem: -1,
      items: this.extractValues("name"),
      types: this.extractValues("type")
    };
   }

  extractValues = (element) => {
     let list = [];
     data.map(x => (
       list.push(x[element])
     ));
     return list;
   }

   handleOpenModal = index => event => {
     this.setState({
       currentItem: index
     });
   }

   handleCloseModal = () => {
     this.setState({
       currentItem: -1
     });
   }

  render() {
    const items = [cabin, cake, circus, game, safe, submarine]
    return (
      <section className="resume-section portfolio d-flex" id="portfolio">
        <div className="container my-auto">
          <h2 className="text-center text-uppercase text-secondary mb-0">Experience</h2>
          <hr className="star-dark mb-5" />
          <div className="row">
            <CreateModalRow items={this.state.items} src={items} type={this.state.types} onClickOpen={this.handleOpenModal} />
            <Modal
              isOpen={this.state.currentItem > -1}
              onRequestClose={this.handleCloseModal}
              bodyOpenClassName="ReactModal__Body--open"
              contentLabel="Experience"
            >
              <ModalContent src={items[this.state.currentItem]} onClick={this.handleCloseModal}/>
            </Modal>

          </div>
        </div>
      </section>
    );
  }
}

export default Experience;



